import React, { useState } from 'react'
import classnames from 'classnames'

import { faRunning, faFilter } from '@fortawesome/free-solid-svg-icons'

import FAIcon from '@src-components/block/fa-icon'

import { AllHookContentQuery } from '@src-queries'

import HookTabItem from './item'

const HooksTab = () => {
	const [openTab, setOpenTab] = useState(0)

	const allHookContentData = AllHookContentQuery()

	const actionHooksData = []
	allHookContentData.find(hookContentData => {
		if (hookContentData.node.frontmatter.hookType === 'action') {
			actionHooksData.push(hookContentData)
		}
	})

	const filterHooksData = []
	allHookContentData.find(hookContentData => {
		if (hookContentData.node.frontmatter.hookType === 'filter') {
			filterHooksData.push(hookContentData)
		}
	})

	return (
		<div className="flex flex-wrap">
			<div className="w-full">
				<ul
					className="flex mb-0 list-none flex-wrap pt-3 pb-4 flex-row"
					role="tablist"
				>
					<li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
						<a
							className={classnames(
								'flex items-center justify-center font-semibold px-4 py-2 shadow-lg rounded leading-normal',
								openTab === 0
									? 'text-white bg-gray-900'
									: 'text-gray-900 bg-white'
							)}
							onClick={event => {
								event.preventDefault()
								setOpenTab(0)
							}}
							data-toggle="tab"
							href="#tab-index-0"
							role="tablist"
						>
							<FAIcon icon={faRunning} className="mr-1" />
							<ruby>
								Action Hooks<rt>アクションフック</rt>
							</ruby>
							<span className="flex items-center justify-center text-sm bg-gray-200 text-gray-600 font-semibold h-6 px-2 rounded-full ml-auto">
								{actionHooksData.length}
							</span>
						</a>
					</li>
					<li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
						<a
							className={classnames(
								'flex items-center justify-center font-semibold px-4 py-2 shadow-lg rounded leading-normal',
								openTab === 1
									? 'text-white bg-gray-900'
									: 'text-gray-900 bg-white'
							)}
							onClick={event => {
								event.preventDefault()
								setOpenTab(1)
							}}
							data-toggle="tab"
							href="#tab-index-1"
							role="tablist"
						>
							<FAIcon icon={faFilter} className="mr-1" />
							<ruby>
								Filter Hooks<rt>フィルターフック</rt>
							</ruby>
							<span className="flex items-center justify-center text-sm bg-gray-200 text-gray-600 font-semibold h-6 px-2 rounded-full ml-auto">
								{filterHooksData.length}
							</span>
						</a>
					</li>
				</ul>
				<div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-4">
					<div className="pt-8 pb-4 flex-auto">
						<div className="tab-content tab-space">
							<div
								className={classnames(openTab === 0 ? 'block' : 'hidden')}
								id="tab-index-0"
							>
								<ul className="grid grid-cols-1 gap-y-8 md:grid-cols-2 md:gap-x-5 md:gap-y-6 xl:grid-cols-3">
									{actionHooksData?.map(edge => {
										return <HookTabItem item={edge} />
									})}
								</ul>
							</div>
							<div
								className={classnames(openTab === 1 ? 'block' : 'hidden')}
								id="tab-index-1"
							>
								<ul className="grid grid-cols-1 gap-y-8 md:grid-cols-2 md:gap-x-5 md:gap-y-6 xl:grid-cols-3">
									{filterHooksData?.map(edge => {
										return <HookTabItem item={edge} />
									})}
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}

export default HooksTab
