import React, { useState } from 'react'
import classnames from 'classnames'

import {
	faHome,
	faAnchor,
	faRunning,
	faFilter,
} from '@fortawesome/free-solid-svg-icons'

import FAIcon from '@src-components/block/fa-icon'

import AppContext from '@src-contexts/app-context'
import SEO from '@src-components/common/seo'

import Layout from '@src-components/layout'

import PageHeading from '@src-components/parts/page-heading'

import HooksTab from '@src-components/parts/hooks-tab'

const HooksListTemplate = props => {
	const { location } = props

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)

	const pageTitle = 'フック一覧'
	const pageDescription = 'Snow Monkey フックの一覧です。'

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		breadcrumbItems: [
			{ label: 'ホーム', url: '/', icon: faHome },
			{ label: pageTitle, url: location.pathname, icon: faAnchor },
		],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="one-column">
				<PageHeading icon={faAnchor}>{pageTitle}</PageHeading>
				<section className="mt-8">
					<HooksTab />
				</section>
			</Layout>
		</AppContext.Provider>
	)
}

export default HooksListTemplate
